<template>
	<div class="page">
		<video src="../assets/background.mp4" autoplay loop muted></video>
		<lucky ref="Lucky" :users="users" :level="level"></lucky>
		<ul class="menu" v-if="showMenu">
			<li v-show="level && !showNext && !isStart" class="active" @click="startLucky">开始抽奖</li>
			<li v-show="level && !showNext && isStart" class="active" @click="stopLucky">停止抽奖</li>
			<li v-show="level && showNext" class="active" @click="next">下一轮</li>
			<li v-show="!isStart" @click="showResultDialog">全部结果</li>
			<!--<li v-show="!isStart" @click="close">关 闭</li>-->
		</ul>
		<div class="reset">
			<img src="../assets/audio.png" alt="">
			<img src="../assets/reset.png" alt="" @click="reset">
		</div>
		<prize-dialog :prize="prize" :visible.sync="showPrize"></prize-dialog>
		<winners-dialog :winners="winners" :visible.sync="showWinners"></winners-dialog>
		<result-dialog :list="prizeRecordList" :users="users" :visible.sync="showResult"></result-dialog>
	</div>
</template>

<script>
	import { Message, MessageBox } from 'element-ui'
	import Lucky from '../components/Lucky';
	import PrizeDialog from '../components/PrizeDialog'
	import WinnersDialog from '../components/WinnersDialog'
	import ResultDialog from '../components/ResultDialog'
	import { queryUserList, queryPrizeList, queryPrizeRecordList, savePrizeRecord, deletePrizeRecord } from '@/api'
	
	export default {
		name: 'prize',
		components:{
			Lucky,
			PrizeDialog,
			WinnersDialog,
			ResultDialog
		},
		data() {
			return {
				users:[],  //参与人员
				isStart: false, //是否正在抽奖
				showMenu: false, //是否显示按钮菜单
				prizeList: [], //奖品信息
				level: 0, //当前抽奖等级
				prizeRecordList: [], //中奖记录
				winners: [], //本次中奖人员
				showPrize: false, //奖品弹框
				showWinners: false, //中奖人弹框
				showResult: false, //全部结果弹框
				showNext: false, //下一步
			}
		},
		computed:{
			prize(){ //本次奖品
				return this.prizeList.find(item=>item.prizeLevel === this.level) || {}
			},
		},
		mounted(){
			this.init();
		},
		methods: {
			async init(){
				await Promise.all([this.queryUserList(),this.queryPrizeList(),this.queryPrizeRecordList()]);

				//从最小的奖项开始
				this.level = this.prizeList[this.prizeList.length-1].prizeLevel;
				this.prizeRecordList.forEach(item=>{
					if(item.prizeLevel <= this.level){
						this.level = item.prizeLevel -1
					}
				});
				await this.$nextTick();
				if(this.level < this.prizeList.length){
					await this.$refs.Lucky.change(true);
				}else{
					await this.$refs.Lucky.changeStart();
				}
				this.showMenu = true;
				if(this.level > 0){
					this.showPrize = true;
				}
			},
			//获取参与人员
			async queryUserList(){
				const res = await queryUserList({pid: this.$store.state.actData.id});
				this.users = [];
				for(let i=0; i< res.data.length; i++){
					this.users.push({
						...res.data[i],
						avatar: await this.getImg(`./users/${res.data[i].id}.jpg`,'users')
					})
				}
			},
			//获取奖品列表
			async queryPrizeList(){
				const res = await queryPrizeList({pid:this.$store.state.actData.id});
				if(!res.data || res.data.length === 0){
					Message({
						message: '暂无奖品信息',
						type: 'error',
						duration: 5 * 1000
					});
					return
				}
				this.prizeList = [];
				for(let i=0; i< res.data.length; i++){
					this.prizeList.push({
						...res.data[i],
						img: await this.getImg(`./prize/${res.data[i].id}.jpg`,'prize')
					})
				}
			},
			//获取中奖记录
			async queryPrizeRecordList(){
				const res = await queryPrizeRecordList({pid:this.$store.state.actData.id});
				this.prizeRecordList = res.data;
			},
			//保存中奖记录
			async savePrizeRecord(winners){
				const prizeRecordJson = winners.map(item=>({
					dept: item.dept,
					userName: item.name,
					prizeId: this.prize.id,
					prizeLevel: this.prize.prizeLevel,
					prizeName: this.prize.prizeName,
				}));
				this.prizeRecordList = this.prizeRecordList.concat(prizeRecordJson);
				await savePrizeRecord({prizeRecordJson});
			},
			//删除中奖记录
			async deletePrizeRecord(){
				await deletePrizeRecord();
				Message({
					message: '重置成功',
					type: 'success',
					duration: 3 * 1000
				});
			},
			getImg(src, dir){
				return new Promise(resolve => {
					const image = new Image();
					image.onerror = ()=>{
						resolve(require(`../assets/${dir}/1.jpg`))
					};
					image.onload = ()=>{
						resolve(src)
					};
					image.src = src;
				})
			},
			startLucky(){
				if(this.prize.amount > (this.users.length - this.prizeRecordList.length)){
					Message({
						message: '剩余抽奖人数不足',
						type: 'warning',
						duration: 5 * 1000
					});
					return
				}
				this.showPrize = false;
				this.isStart = true;
				this.$refs.Lucky.start();
			},
			stopLucky(){
				this.isStart = false;
				this.showNext = true;
				this.$refs.Lucky.stop();
				this.showWinners = true;
				const hasWin = this.prizeRecordList.map(item=>({
					...item,
					name: item.userName
				}));
				this.winners = this.computedWinners(this.prize.amount,hasWin);
				if(this.winners.length === 0){
					return
				}
				this.savePrizeRecord(this.winners);
				this.level --;
			},
			//摇奖
			computedWinners(num = 1,hasWin = []){
				let rtn = [];
				const list = this.users.filter(item=>!hasWin.some(it=>it.dept === item.dept && it.name === item.name));
				if(list.length === 0) return [];
				const L = list.length - 1;
				const index = Math.random()*L | 0;
				rtn.push(list[index]);
				num --;
				if(num > 0){
					const win = JSON.parse(JSON.stringify(hasWin));
					win.push(list[index]);
					rtn = rtn.concat(this.computedWinners(num, win));
				}
				return rtn
			},
			showResultDialog(){
				if(this.prizeRecordList.length === 0){
					Message({
						message: '暂无抽奖记录',
						type: 'warning',
						duration: 3 * 1000
					});
					return
				}
				this.showWinners = false;
				this.showPrize = false;
				this.showResult = true;
			},
			async next(){
				this.showWinners = false;
				this.showNext = false;
				await this.$refs.Lucky.change();
				if(this.level > 0){
					this.showPrize = true;
				}
			},
			async reset(){
				await MessageBox.confirm(
					'是否重置本次抽奖记录！',
					'提示',
					{
						confirmButtonText: '重置',
						cancelButtonText: '取消',
						type: 'warning'
					}
				);
				this.showWinners = false;
				this.showNext = false;
				await this.deletePrizeRecord();
				this.level = this.prizeList[this.prizeList.length-1].prizeLevel;
				await this.$nextTick();
				await this.$refs.Lucky.change();
				this.showPrize = true;
				this.prizeRecordList = [];
				this.winners = [];
			}
		}
	}
</script>
<style lang="scss" scoped>
	.page {
		width: 100%;
		height: 100vh;
		/*background: url("../assets/bg.jpg") 0 0 no-repeat;*/
		/*background-size: 100%;*/
		display: flex;
		align-items: center;
		justify-content: center;
	}
	video{
		position: fixed;
		width: 100%;
		height: 100%;
		object-fit:fill;
	}
	.menu{
		position: fixed;
		z-index: 100;
		width: 100%;
		margin: 0 auto;
		bottom: 30px;
		text-align: center;
		li{
			display: inline-block;
			margin: 0 10px;
			width: 100px;
			height: 30px;
			line-height: 30px;
			border-radius: 15px;
			font-size: 14px;
			background: rgba(255,255,255,.3);
			color: #fff;
			cursor: pointer;
			&:active{
				background: rgba(255,255,255,.5);
				color: #ddd;
			}
			&.active{
				background: #409EFF;
				color: #fff;
				&:active{
					background: #387cdd;
					color: #ddd;
				}
			}
		}
	}
	.reset{
		position: fixed;
		right: 8px;
		height: 120px;
		top: -60px;
		transition: top .3s ease;
		&:hover{
			top: 0;
		}
		img{
			width: 40px;
			height: 40px;
			margin: 8px;
			cursor: pointer;
		}
	}
</style>
