
const calWordWidth = (value, fontSize) => {
	const arr = value.split('');
	const reg = /\w/;
	let	width = 0;
	arr.forEach(item => {
		if (reg.test(item)) {
			width += (fontSize/5)*3  //字母宽度
		} else {
			width += fontSize //汉字宽度
		}
	});
	return width
};

const getImage = (canvas, ctx, W, H) => new Promise((resolve,reject) =>{
	const image = new Image();
	image.onload = function() {
		const rtn = [];
		ctx.clearRect(0, 0, canvas.width, canvas.height);
		ctx.drawImage(image, 0, 0, W, H);
		const imageData = ctx.getImageData(0, 0, W, H);
		const diff = 5;
		for (let j = 0; j < H; j += diff) {
			for (let i = 0; i < W; i += diff) {
				let colorNum = 0
				for (let k = 0; k < diff * diff; k++) {
					let row = k % diff
					let col = ~~(k / diff);
					let r = imageData.data[((j + col) * W + i + row) * 4 + 0];
					let g = imageData.data[((j + col) * W + i + row) * 4 + 1];
					let b = imageData.data[((j + col) * W + i + row) * 4 + 2];
					if (r < 10 && g < 10 && b < 10) colorNum++
				}
				if (colorNum < diff * diff / 3 * 2) {
					rtn.push({
						x: i - (W/2),
						y: j - (H/2),
					});
				}
			}
		}
		resolve(rtn)
	};
	image.onerror = reject;
	image.src = canvas.toDataURL("image/jpeg");
	document.getElementsByTagName('body')[0].appendChild(image)
});


const getTextData = async (value, fontFamily = 'KaiTi') =>{
	const fontSize = 100;
	const	width = calWordWidth(value, fontSize);
	const	canvas = document.createElement('canvas');
	// canvas.id = 'b_canvas'
	canvas.width = width;
	canvas.height = fontSize;
	const ctx = canvas.getContext('2d');
	ctx.font = fontSize + "px " + fontFamily;
	ctx.fillStyle = "orange";
	ctx.fillText(value, 0, fontSize / 5 * 4); //轻微调整绘制字符位置
	return await getImage(canvas, ctx, width, fontSize) //导出为图片再导入到canvas获取图像数据
};


export default getTextData