<template>
	<transition name="fade">
		<div class="bg" v-show="visible" @click="$emit('update:visible',false)">
			<ul>
				<li v-for="item in result">
					<div class="title">{{NumberToChinese(item.prize.prizeLevel)}}等奖：{{item.prize.prizeName}}</div>
					<div class="winners">
						<div class="winner" v-for="winner in item.winners">
							<div class="img">
								<div class="avatar">
									<img :src="winner.avatar" alt="">
								</div>
								<img class="out" src="../../assets/quan_out.png" alt="">
								<img class="in" src="../../assets/quan_in.png" alt="">
							</div>
							<div class="tag">
								<span>{{winner.name}}</span>
								<span>{{winner.dept}}</span>
							</div>
						</div>
					</div>
				</li>
			</ul>
		</div>
	</transition>
</template>

<script>
	import NumberToChinese from '@/utils/NumberToChinese'
	export default {
		props:{
			list:{
				type:Array,
				default: []
			},
			users:{
				type:Array,
				default: []
			},
			visible:{
				type:Boolean,
				default: false
			}
		},
		computed:{
			result(){
				const levels = [];
				this.list.forEach(item=>{
					if(levels.every(it=>it.prizeLevel !== item.prizeLevel)){
						levels.push(item)
					}
				});
				return levels.map(item=> ({
					prize: item,
					winners: this.list
						.filter(it=>it.prizeLevel === item.prizeLevel)
						.map(ele=>this.users
							.find(el=>el.name === ele.userName && el.dept === ele.dept) || {})
				})).sort((a,b)=>a.prize.prizeLevel - b.prize.prizeLevel)
			}
		},
		methods:{
			NumberToChinese,
		}
	}
</script>

<style lang="scss" scoped>
	.bg{
		position: fixed;
		top: 0;
		left: 0;
		background: rgba(0,0,0,.5);
		z-index: 101;
		width: 100vw;
		height: 100vh;
		overflow: auto;
		ul{
			width: 100%;
		}
		li{
			color: #fff;
			margin: 0 10px;
			.title{
				font-size: 18px;
				margin: 16px auto;
				width: 700px;
			}
			.winners{
				margin: 0 auto;
				width: 800px;
				display: flex;
				align-items: center;
				justify-content: center;
				flex-wrap: wrap;
				.winner{
					width: 80px;
					margin: -12px 12px;
					display: flex;
					align-items: center;
					justify-content: center;
					flex-direction: column;
					.img{
						font-size: 0;
						position: relative;
						width: 90px;
						height: 85px;
						img{
							position: absolute;
							top: 50%;
							left: 50%;
						}
						.out{
							margin: -45px;
							width: 90px;
							height: 90px;
						}
						.in{
							margin: -44px;
							width: 88px;
							height: 88px;
						}
						.avatar{
							position: absolute;
							top: 50%;
							left: 50%;
							transform: translate(-50%,-50%);
							width: 69px;
							height: 69px;
							border-radius: 50%;
							overflow: hidden;
							img{
								width: 100%;
								position: absolute;
								top: 50%;
								left: 50%;
								transform: translate(-50%,-50%);
							}
						}
						.in{
							animation: rotate 3s infinite linear;
							@keyframes rotate {
								0%{
									transform: rotate(0deg);
								}
								50%{
									transform: rotate(180deg);
								}
								100%{
									transform: rotate(360deg);
								}
							}
						}
						.out{
							animation: rotate1 6s infinite linear;
							@keyframes rotate1 {
								0%{
									transform: rotate(0deg);
								}
								50%{
									transform: rotate(-180deg);
								}
								100%{
									transform: rotate(-360deg);
								}
							}
						}
					}
					.tag{
						transform: scale(0.5) translateY(-32px);
						color: #000080;
						width: 160px;
						height: 76px;
						background: url("../../assets/tag.png") 0 0 no-repeat;
						background-size: 100%;
						display: flex;
						align-items: center;
						flex-direction: column;
						justify-content: center;
						span:nth-of-type(1){
							font-size: 24px;
							font-weight: bold;
						}
						span:nth-of-type(2){
							font-size: 18px;
						}
					}
				}
			}
		}
	}
</style>