<template>
	<div class="scene">
		<div
			class="box"
			:style="boxStyle"
		>
			<ul class="ring">
				<li
					v-for="(item,index) in list"
					:style="{
	            opacity: item.opacity,
	            transform: item.transform,
	            transition: item.transition
	          }"
					:key="index"
				>
					<div class="avatar">
						<img :src="item.avatar" alt="">
					</div>
				</li>
			</ul>
		</div>
		<div>
			<img v-for="(item,index) in list" v-show="showCenter && index === showIndex" class="center" :src="item.avatar" alt="">
		</div>
	</div>
</template>

<script>
	import getTextData from './getTextData'
	
	const r = 320;
	
	const timeout = time => new Promise(resolve => setTimeout(resolve,time));
	
	export default {
		props:{
			users:{
				type: Array,
				default: []
			},
			level:{
				type: Number,
				default: 0
			}
		},
		data() {
			return {
				list: [],
				boxStyle: {},
				angleX: 0,
				angleY: 0,
				showIndex: 0,
				showCenter: false,
			}
		},
		methods: {
			//变化开始
			async changeStart(){
				//2020年年会
				const textData1 = await getTextData('2020年年会');
				this.changeToText(textData1);
				this.changeToText(textData1).closeUp();
				this.transparent();
				await timeout(300);
				this.changeToText(textData1).closeUp();
				this.transition(2);
				this.autoRotate('text1');
				await timeout(3000);
				this.changeToText(textData1).dispersal();
				this.transition(2);
				await timeout(3000);
				//抽奖环节
				const textData2 = await getTextData('抽奖环节');
				this.changeToText(textData2);
				this.transparent();
				this.changeToText(textData2).dispersal();
				await timeout(300);
				this.changeToText(textData2).closeUp();
				this.transition(2);
				this.autoRotate('text2');
				await timeout(5000);
				this.changeToText(textData2).dispersal();
				this.transition(2);
				await timeout(2000);
				//倒计时
				await this.clock();
				this.changeToCircle().dispersal();
				this.transparent();
				await timeout(300);
				await this.change();
			},
			//变化形态
			async change(isFirst){
				if(isFirst){
					this.changeToCone().dispersal();
					this.transparent();
					await timeout(300);
				}
				const arr = [
					'drawCone',
					// 'drawColumn',
					'drawColumn2',
					'drawCircle',
				];
				const index = this.level > 0 ? this.level - 1 : arr.length - 1;
				
				await this[arr[index%arr.length]]();
			},
			//画球
			async drawCircle(){
				this.autoRotate('rotate');
				this.changeToCircle().closeUp();
				this.transition(2);
				await timeout(2000)
			},
			//画柱1
			async drawColumn(){
				this.autoRotate('rotate');
				this.changeToColumn().closeUp();
				this.transition(2);
				await timeout(2000)
			},
			//画柱2
			async drawColumn2(){
				this.autoRotate('rotate');
				this.changeToColumn2().closeUp();
				this.transition(2);
				await timeout(2000)
			},
			//画锥
			async drawCone(){
				this.autoRotate('rotate');
				this.changeToCone().closeUp();
				this.transition(2);
				await timeout(2000)
			},
			//倒计时
			async clock(){
				this.autoRotate('stop');
				for (let i=0; i<3;i++){
					await this.changeToNum(3-i);
				}
			},
			//倒计时数字
			async changeToNum(num){
				const textNum3 = await getTextData(String(num));
				this.changeToText(textNum3);
				this.transparent();
				this.changeToText(textNum3).dispersal();
				await timeout(300);
				this.changeToText(textNum3).closeUp();
				this.transition(3);
				await timeout(2000);
				this.changeToText(textNum3).dispersal();
				this.transition(3);
				await timeout(1200)
			},
			//透明
			transparent(){
				this.list.forEach((item) => {
					item.opacity = 0;
				});
			},
			//过渡
			transition(type){
				this.list.forEach((item) => {
					if(type === 1){
						item.transition ='opacity .6s';
					}
					if(type === 2){
						item.transition ='all .6s ' + Math.random() + 's';
					}
					if(type === 3){
						item.transition ='all .6s ' + Math.random()*.3 + 's';
					}
				});
			},
			//旋转
			async autoRotate(type) {
				if(type === 'text1'){
					this.boxStyle = {
						animation: 'text1 6s linear infinite'
					}
				}
				if(type === 'text2'){
					this.boxStyle = {
						animation: 'text2 12s linear infinite'
					}
				}
				if(type === 'rotate'){
					this.boxStyle = {
						animation: 'rotate 12s linear infinite'
					}
				}
				if(type === 'stop'){
					this.boxStyle = {}
				}
			},
			//画文字
			changeToText(textData){
				this.list = [];
				this.list = textData.map((item,index)=>JSON.parse(JSON.stringify(this.users[index%(this.users.length)])));
				//收拢
				const closeUp = ()=>{
					this.list.forEach((item,index) => {
						item.opacity = 1;
						item.transform = `translate3D(${textData[index].x*8}px,${textData[index].y*8}px,${-1580}px) rotateY(0) rotateX(0)`;
					});
				};
				//散开
				const dispersal= ()=>{
					this.list.forEach((item,index)=>{
						item.opacity = 1;
						item.transform = `translate3D(${textData[index].x*20*Math.random()}px,${textData[index].y*20*Math.random()}px,${1600}px) rotateY(0) rotateX(0)`;
					});
				};
				return {closeUp,dispersal}
			},
			//画球 200个图像
			changeToCircle() {
				const photos = Array.apply(null, Array(200)).map((item,index)=>JSON.parse(JSON.stringify(this.users[index%(this.users.length)])));
				this.list = this.setCircle(photos);
				//收拢
				const closeUp = ()=>{
					this.list.forEach(item => {
						item.opacity = 1;
						item.transform = `translate3D(${item.circleX}px,${item.circleY}px,${item.circleZ}px) rotateY(${item.circlePhi}rad) rotateX(${item.circleTheta - Math.PI / 2}rad)`;
					});
				};
				//散开
				const dispersal= ()=>{
					this.list.forEach(item => {
						item.opacity = 1;
						item.transform = `translate3D(${item.bigCircleX}px,${item.bigCircleY}px,${item.bigCircleZ}px) rotateY(${item.circlePhi}rad) rotateX(${item.circleTheta - Math.PI / 2}rad)`;
					});
				};
				return {closeUp,dispersal}
			},
			
			//画柱 200个图像
			changeToColumn(){
				const photos = Array.apply(null, Array(200)).map((item,index)=>JSON.parse(JSON.stringify(this.users[index%(this.users.length)])));
				this.list = this.setColumn(photos);

				const circleArr = this.getCircleArr(photos);
				const columnH = Math.floor(photos.length / (circleArr.length - 2));
				const columnMum = columnH * (circleArr.length - 2);
				//收拢
				const closeUp = ()=>{
					this.list.forEach((item,index) => {
						if(index >= columnMum) {
							item.opacity = 0;
							return
						}
						item.opacity = 1;
						item.transform = `translate3D(${item.columnX}px,${item.columnY}px,${item.columnZ}px) rotateY(${item.columnPhi}rad) rotateX(0rad)`;
					});
				};
				//散开
				const dispersal= ()=>{
					this.list.forEach(item => {
						if(index >= columnMum) {
							item.opacity = 0;
							return
						}
						item.opacity = 1;
						item.transform = `translate3D(${item.bigColumnX}px,${item.bigColumnY}px,${item.bigColumnZ}px) rotateY(${item.columnPhi}rad) rotateX(0rad)`;
					});
				};
				return {closeUp,dispersal}
			},

			changeToColumn2(){
				const photos = Array.apply(null, Array(200)).map((item,index)=>JSON.parse(JSON.stringify(this.users[index%(this.users.length)])));
				this.list = this.setColumn(photos);

				const circleArr = this.getCircleArr(photos);
				const columnH = Math.floor(photos.length / (circleArr.length - 2));
				const columnMum = columnH * (circleArr.length - 2);
				//收拢
				const closeUp = ()=>{
					this.list.forEach((item,index) => {
						if(index >= columnMum) {
							item.opacity = 0;
							return
						}
						item.opacity = 1;
						item.transform = `translate3D(${item.column2X}px,${item.column2Y}px,${item.column2Z}px) rotateY(${item.column2Phi}rad) rotateX(0rad)`;
					});
				};
				//散开
				const dispersal= ()=>{
					this.list.forEach(item => {
						if(index >= columnMum) {
							item.opacity = 0;
							return
						}
						item.opacity = 1;
						item.transform = `translate3D(${item.bigColumn2X}px,${item.bigColumn2Y}px,${item.bigColumn2Z}px) rotateY(${item.column2Phi}rad) rotateX(0rad)`;
					});
				};
				return {closeUp,dispersal}
			},
			
			changeToCone(){
				const photos = Array.apply(null, Array(200)).map((item,index)=>JSON.parse(JSON.stringify(this.users[index%(this.users.length)])));
				const { list, coneNum } = this.setCone(photos);
				this.list = list;
				//收拢
				const closeUp = ()=>{
					this.list.forEach((item,index) => {
						if(index >= coneNum) {
							item.opacity = 0;
							return
						}
						item.opacity = 1;
						item.transform = `translate3D(${item.coneX}px,${item.coneY}px,${item.coneZ}px) rotateY(${item.conePhi}rad) rotateX(${item.coneTheta}rad)`;
					});
				};
				//散开
				const dispersal= ()=>{
					this.list.forEach((item,index) => {
						if(index >= coneNum) {
							item.opacity = 0;
							return
						}
						item.opacity = 1;
						item.transform = `translate3D(${item.bigConeX}px,${item.bigConeY}px,${item.bigConeZ}px) rotateY(${item.conePhi}rad) rotateX(${item.coneTheta}rad)`;
					});
				};
				return {closeUp,dispersal}
			},
			
			getCircleArr(length) {
				let circleArr = [];
				let wordNum = -1;
				let layer = 0;
				let num = 0;
				for (let i = 4; i < 13; i++) {
					num = i * i + (i + 1) * (i + 1);
					if (num >= length) {
						layer = (i - 1) * 2 + 1;
						break;
					}
					layer = (i - 1) * 2 + 1;
				}
				for (let i = 0; i < layer; i++) {
					if (i < (layer + 1) / 2) {
						wordNum += 2;
					} else {
						wordNum -= 2;
					}
					circleArr.push(wordNum);
				}
				return circleArr
			},
			setCircle(photos){
				const list = [];
				let num = 0;
				const circleArr = this.getCircleArr(photos.length);
				for (let i = 0; i < circleArr.length; i++) {
					const theta = Math.PI / circleArr.length;
					const phi = 2 * Math.PI / circleArr[i];
					for (let j = 0; j < circleArr[i]; j++) {
						const item = photos[num];
						num++;
						if(item){
							this.setCircleItem(item, circleArr, theta, phi, i, j);
							list.push(item)
						}
					}
				}
				return list
			},
			setCircleItem( item, circleArr, theta, phi, i, j) {
				item.circleX = r * Math.sin(theta * i) * Math.sin(phi * j) ;
				item.circleY = -r * Math.cos(theta * i) ;
				item.circleZ = r * Math.sin(theta * i) * Math.cos(phi * j);
				item.circleTheta = theta * (circleArr.length - i);
				item.circlePhi = phi * j;

				item.bigCircleX = (r + 2000) * Math.sin(theta * i) * Math.sin(phi * j);
				item.bigCircleY = -(r + 2000) * Math.cos(theta * i);
				item.bigCircleZ = (r + 2000) * Math.sin(theta * i) * Math.cos(phi * j);
			},
			
			setColumn(photos){
				const list = [];
				let num = 0;
				const circleArr = this.getCircleArr(photos.length);
				const columnH = Math.floor(photos.length / (circleArr.length - 2));
				for (let i = 0; i < circleArr.length - 1; i++) {
					const phi = 2 * Math.PI / columnH;
					for (let j = 0; j < columnH; j++) {
						const item = photos[num];
						this.setColumnItem(item, circleArr, phi, i, j);
						this.setColumnItem2(item, circleArr, phi, i, j);
						list.push(item);
						num++;
					}
				}
				return list
			},

			setColumnItem(item, circleArr, phi, i, j) {
				item.columnX = r / 1.5 * Math.sin(phi * j);
				item.columnY = (2 * r / (circleArr.length - 2)) * i - 300;
				item.columnZ = (r / 1.5 * Math.cos(phi * j)).toFixed(2);
				item.columnPhi = phi * j;
				item.bigColumnX = (r + 2000) / 1.5 * Math.sin(phi * j);
				item.bigColumnY = (2 * (r + 2000) / (circleArr.length - 2)) * i + 50 - 2000;
				item.bigColumnZ = ((r + 2000) / 1.5 * Math.cos(phi * j)).toFixed(2);
			},

			setColumnItem2(item, circleArr, phi, i, j){
				item.column2X = r / 1.5 * Math.sin(phi * j + i * Math.PI / 180 * 8) ;
				item.column2Y = (2 * r / (circleArr.length - 2)) * i - 300;
				item.column2Z = (r / 1.5 * Math.cos(phi * j + i * Math.PI / 180 * 8)).toFixed(2);
				item.column2Phi = phi * j + i * Math.PI / 180 * 8;
				item.bigColumn2X = (r + 2000) / 1.5 * Math.sin(phi * j + i * Math.PI / 180 * 8) ;
				item.bigColumn2Y = (2 * (r + 2000) / (circleArr.length - 2)) * i + 50 - 2000;
				item.bigColumn2Z = ((r + 2000) / 1.5 * Math.cos(phi * j + i * Math.PI / 180 * 8)).toFixed(2);
			},
			
			setCone(photos){
				const list = [];
				let coneNum = 0;
				const coneArr = [];
				let phi = 0;
				for (let i = 0; i < photos.length; i++) {
					coneNum += 2 * i + 1;
					if (coneNum > photos.length) {
						coneNum -= 2 * i + 1;
						break;
					}
					coneArr.push(2 * i + 1);
				}
				let num = 0;
				for (let i = 0; i < coneArr.length; i++) {
					phi = 2 * Math.PI / coneArr[i];
					for (let j = 0; j < coneArr[i]; j++) {
						const item = photos[num];
						this.setConeItem(item, phi, i, j,coneArr);
						list.push(item);
						num++;
					}
				}
				return { list, coneNum }
			},

			setConeItem(item,phi,i,j,coneArr){
				item.coneX = (2 * r / coneArr.length) * i * Math.tan(30 * Math.PI / 180) * Math.sin(phi * j);
				item.coneY = (2 * r / coneArr.length) * i - 380;
				item.coneZ = (2 * r / coneArr.length) * i * Math.tan(30 * Math.PI / 180) * Math.cos(phi * j);
				item.coneTheta = Math.PI / 6;
				item.conePhi = phi * j;
				item.bigConeX = (2 * (r + 2000) / coneArr.length) * i * Math.tan(30 * Math.PI / 180) * Math.sin(phi * j);
				item.bigConeY = (2 * (r + 2000) / coneArr.length) * i - 380 - 2000;
				item.bigConeZ = (2 * (r + 2000) / coneArr.length) * i * Math.tan(30 * Math.PI / 180) * Math.cos(phi * j);
			},
			
			start(){
				if(this.timer)clearInterval(this.timer);
				this.showCenter = true;
				this.timer = setInterval(() => {
					this.showIndex = Math.random()*(this.list.length-1) | 0;
				}, 20)
			},
			
			stop(){
				this.showCenter = false;
				if(this.timer)clearInterval(this.timer);
			}
		}
	}
</script>

<style lang="scss" scoped>

	.scene {
		width: 400px;
		height: 400px;
		perspective: 900px;
		-webkit-perspective: 900px;
		position: relative;
		transform-style: preserve-3d;
		-webkit-transform-style: preserve-3d;
		color: #fff;
	}
	
	.box {
		width: 100%;
		height: 100%;
		position: relative;
		transform-style: preserve-3d;
		-webkit-transform-style: preserve-3d;
		perspective-origin: center 250px;
	}
	
	.ring {
		width: 100%;
		height: 100%;
		transform-style: preserve-3d;
		-webkit-transform-style: preserve-3d;
		position: absolute;
		li {
			font-size: 0;
			text-align: center;
			position: absolute;
			top: 50%;
			left: 50%;
			margin-top: -18px;
			margin-left: -18px;
			/*transition: all .6s;*/
			opacity: 0;
			.avatar{
				width: 36px;
				height: 36px;
				border-radius: 4px;
				overflow: hidden;
				display: flex;
				align-items: center;
				img{
					width: 100%;
				}
			}
		}
	}
	
	.center{
		width: 120px;
		height: 120px;
		border-radius: 15px;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);
	}
	
</style>
<style>
	@keyframes text1 {
		0%{
			transform: rotateX(0deg) rotateY(4deg) rotateZ(0deg)
		}
		50%{
			transform: rotateX(0deg) rotateY(-4deg) rotateZ(0deg)
		}
		100%{
			transform: rotateX(0deg) rotateY(4deg) rotateZ(0deg)
		}
	}
	@keyframes text2 {
		0%{
			transform:translateZ(0px) rotateX(0deg) rotateY(-30deg) rotateZ(0deg)
		}
		50%{
			transform:translateZ(-300px) rotateX(0deg) rotateY(30deg) rotateZ(0deg)
		}
		100%{
			transform:translateZ(0px) rotateX(0deg) rotateY(-30deg) rotateZ(0deg)
		}
	}
	@keyframes rotate {
		0%{
			transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg)
		}
		50%{
			transform: rotateX(0deg) rotateY(180deg) rotateZ(30deg)
		}
		100%{
			transform: rotateX(0deg) rotateY(360deg) rotateZ(0deg)
		}
	}
</style>