				</div>
				<p>{{NumberToChinese(prize.prizeLevel)}}等奖：{{prize.prizeName}}</p>
			</li>
		</ul>
	</transition>
</template>

<script>
	import NumberToChinese from '@/utils/NumberToChinese';
	
	export default {
		props:{
