<template>
	<transition name="fade">
		<div class="bg" v-show="visible" @click="$emit('update:visible',false)">
			<ul>
				<li v-for="item in winners">
					<div class="img">
						<div class="avatar">
							<img :src="item.avatar" alt="">
						</div>
						<img class="out" src="../../assets/quan_out.png" alt="">
						<img class="in" src="../../assets/quan_in.png" alt="">
					</div>
					<div class="tag">
						<span>{{item.name}}</span>
						<span>{{item.dept}}</span>
					</div>
				</li>
			</ul>
		</div>
	</transition>
</template>

<script>
	export default {
		props:{
			winners:{
				type: Array,
				default: []
			},
			visible:{
				type: Boolean,
				default: false
			}
		},
		data() {
			return {}
		},
	}
</script>

<style lang="scss" scoped>
	.bg{
		position: fixed;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		background: rgba(0,0,0,.5);
		z-index: 10;
		display: flex;
		align-items: center;
		justify-content: center;
		ul{
			width: 1600px;
			display: flex;
			align-items: center;
			justify-content: center;
			flex-wrap: wrap;
		}
		li{
			margin: 0 10px;
			display: flex;
			align-items: center;
			justify-content: center;
			flex-direction: column;
			.img{
				font-size: 0;
				position: relative;
				width: 180px;
				height: 170px;
				img{
					position: absolute;
					top: 50%;
					left: 50%;
				}
				.out{
					margin: -90px;
					width: 180px;
					height: 180px;
				}
				.in{
					margin: -88px;
					width: 176px;
					height: 176px;
				}
				.avatar{
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%,-50%);
					width: 138px;
					height: 138px;
					border-radius: 50%;
					overflow: hidden;
					img{
						width: 100%;
						position: absolute;
						top: 50%;
						left: 50%;
						transform: translate(-50%,-50%);
					}
				}
				.in{
					animation: rotate 3s infinite linear;
					@keyframes rotate {
						0%{
							transform: rotate(0deg);
						}
						50%{
							transform: rotate(180deg);
						}
						100%{
							transform: rotate(360deg);
						}
					}
				}
				.out{
					animation: rotate1 6s infinite linear;
					@keyframes rotate1 {
						0%{
							transform: rotate(0deg);
						}
						50%{
							transform: rotate(-180deg);
						}
						100%{
							transform: rotate(-360deg);
						}
					}
				}
			}
			.tag{
				color: #000080;
				width: 140px;
				height: 68px;
				background: url("../../assets/tag.png") 0 0 no-repeat;
				background-size: 100%;
				display: flex;
				align-items: center;
				flex-direction: column;
				justify-content: center;
				span:nth-of-type(1){
					font-size: 18px;
					font-weight: bold;
					margin: 2px 0;
				}
				span:nth-of-type(2){
					font-size: 14px;
				}
			}
		}
	}
</style>